





import { Component, Model, Prop, Vue, Watch, Emit } from 'vue-property-decorator';

@Component
export class SearchForm extends Vue {
    private data() {
        return {
            search: '',
        };
    }
    @Watch('search')
    @Emit('search:text')
    private onSeachChanged(val: string, oldVal: string) {
        return val;
    }
}
export default SearchForm;
